@import url('https://fonts.googleapis.com/css2?family=Gothic+A1&display=swap');

* {
    font-family: 'Gothic A1', sans-serif;
}

h1 {
    text-align: center;
}

.main-content {
   text-align: center;
   width: 50vw;
   max-width: 700px;
   min-width: 300px;
   display: block;
   margin: 25px auto;
   border: 2px outset green;
   border-radius: 3%;
   padding: 5px;
}

.selection-instruction {
    text-align: center;
    font-style: italic;
    margin-bottom: 1px;
}

select {
    text-align: left;
    margin-bottom: 15px;
}

h6 {
    border-bottom: 2px dotted green;
    text-align: center;
    margin-bottom: 15px;
}

h6 > span {
    color: green;
}

.product-title {
    color: green;
}

.product {
    text-align: center;
}

img {
    width: 300px;
    height: 200px;
    margin: 0 auto;
    display: block;
    object-fit: contain;
    /*prevent squeezing on small-screen devices*/
}

.product-description {
    text-align: center;
}